@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;600&display=swap');

*{
    margin: 0;
    padding: 0;
}

body{
    background: #fff;
    color: #222;
    font-family: 'Montserrat', sans-serif;
    font-weight: 300;
}

.wrapper{
    max-width: 1280px;
    margin: 50px auto;
    padding: 0 10px;
}

@media (max-width:1280px){
    .wrapper{
        max-width: 970px;
    }
}
@media (max-width:992px){
    .wrapper{
        max-width: 750px;
    }
}
@media (max-width:768px){
    .wrapper{
        max-width: none;
    }
}

header .logo{
    font-weight: 600;
    font-size: 20px;
}

header ul.nav{
    float: right;

}

header ul.nav li{
    display: inline;
    margin-left: 25px;
    cursor: pointer;
    transition: opacity 400ms ease;
}

header ul.nav li:hover{
    opacity: 0.6;
}

header .presentation{
    margin: 50px 0;
    background: url('./img/back.jpg') no-repeat;
    width: 100%;
    height: 500px;
    background-size: cover;
    background-position: center center;
    background-blend-mode: multiply;
    background-color: #9e9e9e;
    position: relative;
}

header .presentation:after{
    content: 'Очки с недочетами по скидке';
    position: absolute;
    top: 100px;
    left: 50px;
    font-size: 40px;
    font-weight: 600;
    width: 300px;
    color: #fff;
}

header .presentation:before{
    content: 'от -40% до -80%';
    position: absolute;
    top: 240px;
    left: 50px;
    font-size: 15px;
    font-weight: 600;
    width: 300px;
    color: #fff;
}

main{
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    justify-content: space-between;
}

main .item{
    width: 30%;
    margin-bottom: 50px;
    background: #f5f5f5;
    overflow: hidden;
    position: relative;
    padding-bottom: 20px;
    border-radius: 10px 10px 0 0;
}

@media (max-width:768px){
    main .item{
        width: 48%;
        justify-content: space-between;
    }
}

main .item img{
    width: 100%;
    border-radius: 10px 10px 0 0;
    transition: transform 500ms ease;
}

main .item img:hover{
    transform: scale(1.05);
}

main h2, main p, main b {
    margin: 10px 20px;
    color: #333;
}

main .add-to-cart{
    position: absolute;
    right: 20px;
    bottom: 20px;
    background: #ca5252;
    width: 35px;
    height: 35px;
    text-align: center;
    line-height: 35px;
    color: #fff;
    border-radius: 50%;
    cursor: pointer;
    font-weight: 600;
    transition: transform 300ms ease;
}

@media (max-width:768px){
    main .add-to-cart{
        right: 0px;
        bottom: 0px;

        margin: 10px 10px;
        vertical-align:bottom;
    }
}

main .add-to-cart:hover{
    transform: scale(1.5);
}

footer{
    text-align: center;
    margin-top: 50px;
}